
.container {
    width: 600px;
    /*height: 233px;*/
    display: flex;

    flex-wrap: wrap;
    justify-content: space-between;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;

}

